.todos-container {
  width: 500px;
  background: #949ea4;
  padding: 2rem;
  border-radius: 5px;
  text-align: center;
  margin: auto;

  .completed-todos-message {
    display: flex;
    justify-content: center;
    margin-top: 15px;
    color: #2e7d32;
    font-weight: bold;
  }

  .todos-error {
    display: flex;
    justify-content: center;
    margin-top: 10px;
    color: #c71b1b;
    font-size: 15px;
    font-weight: 500;
  }
}
